export const getGifs = async (category) => {

	const url = `https://api.giphy.com/v1/gifs/search?api_key=dOJqDFu010oFScZWCJV8lBfEmS1HWtKo&q=${encodeURI(category)}&limit=4`
	const resp = await fetch(url)
	const {data} = await resp.json()
	const gifs = data.map(
		img => {
			return {
				id: img.id,
				title: img.title,
				url: img.images?.downsized_medium.url
			}
		})
	return gifs
}

